@import 'varibles'
    


.container-abouSkills
    width: 100vw
    height: 100vh 
    position: relative
    
    

.container-abouSkills img
    position: absolute
    right: -10%
    top: -15%
    transform: scale(0.6)
    transition: 1.5s ease-in-out
    margin-right: 5%
    
    

.container-sobre
    width: 40vw
    height: auto
    font-size: 1.8em
    position: relative
    left: 40px
    top: 15%
    font-family: $fontItim
    line-height: 1.8
    background-color: #ffffff2c
    border-radius: 25px
    text-align: left
    padding: 20px


    
        
   
    