@import 'varibles'
@import 'animations'
@import 'darkmode'
@import 'pc_e_lampada'
@import 'stilosobre'
@import 'stilohabilidades'
@import 'stiloprojetos'
@import 'mixin'

html 
    scroll-behavior: smooth
    height: 100vh
   

body
    margin: 0
    padding: 0
    color: var(--cor-texto) 
    overflow-x: hidden
    

.home
    position: relative
    width: 100vw
    height: 100vh


.container-navbar
    position: fixed
    right: 0    
    width: 60%
    height: 50px
    display: flex
    justify-content: flex-end
    align-items: center
    z-index: 99999999999
    

    & .navbar   
        display: flex
        gap: 30px
        margin-right: 40px
       
    
a.link-navbar
    position: relative // Necessário para que o ::after seja posicionado corretamente
    text-decoration: none
    font-family: $fontItim
    font-size: 1.3em
    color: inherit
    transition: 0.3s ease-in-out

    &:hover
        transform: scale(1.3)
        

    &::after
        content: ""
        position: absolute
        bottom: 0
        left: 0
        height: 3px
        width: 0 // Inicialmente sem largura
        background-color: #005431
        border-radius: 25px
        transition: width 0.3s ease-in-out // Suaviza a transição

.link-navbar.borda-ativa::after
    animation: borda-navbar 0.5s forwards

.link-navbar.retirar-borda::after
    animation: retirar-borda-navbar 0.5s forwards

       


.container-apresentacao
    position: relative
    right: -55%
    width: auto
    max-width: 30%
    height: auto
    white-space: nowrap
    font-size: 3em
    font-family: $fontItim  
    z-index: 1

    & h1
        position: relative
        top: -50px
        right: 50px
        display: block
        animation: fade-in-slide 1.5s ease-out both

.alternar-escrita
    position: relative
    font-size: 3.3vw
    color: #089e7b
    font-weight: bold
    top: -30px
    right: 50px
      
    &::before
            content: "Desenvolvedor"
            font-weight: normal
            color: var(--texto-desenvolvedor)
            margin-right: 10px 
            display: inline-block


.click-container
    position: fixed
    width: 100%
    height: 100%
    pointer-events: none
    z-index: 9999999999999999999999
    top: 0
    left: 0

    .splash
        position: absolute
        animation: fadeOut 2s forwards
        opacity: 1
        transform: scale(1)

        & img
            width: 50px 
            height: 50px 
            pointer-events: none 
            color: #fff







           
  